import React, { useState } from 'react';
import { connect } from 'react-redux'
import { Route } from 'react-router-dom'
import MapContainer from '../components/MapContainer/MapContainer'

import { Header, LoginForm, RegistrationForm } from '../components'
import { Flex } from '../components'
import { PharmCard } from '../components'
import { CardDesc, Footer, Pagination } from '../components'
import Forum from './Forum/Forum'
import AboutUs from './AboutUs/AboutUs'
import PeopleHelp from './PeopleHelp/PeopleHelp'
import './App.scss';




const cccc =  {
  name: "InLek Аптека N36 ОДО ДКМ-Фарм",
  address: "Минск-Октябрьский, аг. Сеница, ул. Зеленая, 1, к. 5, маг. Гиппо",
  phone: "(017) 5107078",
  detailHref: "https://tabletka.by/pharmacies/1611",
  id: 1611,
  workTime: [
    {
      day: "Понедельник",
      time: "09:00—22:00"
    },
    {
      day: "Вторник",
      time: "09:00—22:00"
    },
    {
      day: "Среда",
      time: "09:00—22:00"
    },
    {
      day: "Четверг",
      time: "09:00—22:00"
    },
    {
      day: "Пятница",
      time: "09:00—22:00"
    },
    {
      day: "Суббота",
      time: "09:00—22:00"
    },
    {
      day: "Воскресенье",
      time: "09:00—22:00"
    }
  ],
  srcImg: "https://tabletka.by/img/pharmacies/1611-mi_pict_apt.jpg",
  coords: [
    "53.822134",
    "27.530765"
  ]
}

const App = (props) => {
  const { items } = props.cardsItem
  const [cardDes, setCardDes] = useState(false);
  const [cardObj, setCardObj] = useState(cccc);

  const hendlerCard = (e) => {
    setCardDes(true)
    setCardObj(e)
  }

  const closeCard = () => {
    setCardDes(false)
  }
  return (
    <div>
      <CardDesc item={cardObj} active={cardDes} closeCard={closeCard} />

      <Header />

      <Route path='/login' component={LoginForm} />
      <Route path='/registration' component={RegistrationForm} />

      <Route exact path='/' component={() => {
        return (<div>

          <div className="map">
            <div className="map-container">
              <MapContainer />
            </div>

          </div>
          <div className="wrapper">
          <Flex>

            {
              items.map(item => (
                <PharmCard
                  item={item}
                  img={item.srcImg}
                  name={item.name}
                  phone={item.phone}
                  key={item.id}
                  address={item.address}
                  onclick={hendlerCard}
                  
                />))
            }

          </Flex>
          <Pagination />

            </div>
        </div>
        )
      }

      } />
      <Route path='/help' component={PeopleHelp} />
      <Route path='/forum' component={Forum} />
      <Route path='/about' component={AboutUs} />
      <Footer />
    </div>
  );
}


const mapStateToProps = state => {
  return {
    cardsItem: state.cards.cards
  };
};


export default connect(mapStateToProps)(App);
