import React from 'react';
import { Form, Icon, Input, Button, Checkbox } from 'antd';
import { NavLink } from 'react-router-dom';
import './LoginForm.scss';


const LogForm = (props) => {
  function handleSubmit(e) {
    e.preventDefault();
    props.form.validateFields((err, values) => {
      if (!err) {
        console.log('Received values of form: ', values);
      }
    });
  };
  const { getFieldDecorator } = props.form;
  return (
    <div className="form-container">
      <div className="form-wrap">
        <Form onSubmit={handleSubmit} className="login-form">

          <h2 className="form-title">Войдите на сайт</h2>
          <Form.Item>
            {getFieldDecorator('username', {
              rules: [{ required: true, message: 'Пожалуйста введите ваше имя!' }],
            })(
              <Input
                prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
                placeholder="emal или телефон"
                className="form-input"
              />,
            )}
          </Form.Item>
          <Form.Item>
            {getFieldDecorator('password', {
              rules: [{ required: true, message: 'Пожалуйста введите ваш пароль!' }],
            })(
              <Input
                prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
                type="password"
                placeholder="Пароль"
                className="form-input"
              />,
            )}
          </Form.Item>
          <Form.Item>
            <Button type="primary" htmlType="submit" className="login-form-button">
              Войти
            </Button>
            <span className="form-text">Или</span> <NavLink to="/registration" className="form-text" activeClassName="selected">
              Зарегистрироваться!
               </NavLink>
          </Form.Item>
        </Form>
      </div>

    </div>
  );
}

const LoginForm = Form.create({ name: 'login_form' })(LogForm);

export default LoginForm;




